"use client"
import React, { useState } from 'react'
import { TbLayoutSidebarLeftCollapse,TbLayoutSidebarRightCollapse } from "react-icons/tb";

interface contentProps{
    recieveSideToggle: (active: boolean) => void
}
const Content:React.FC<contentProps> = ({recieveSideToggle}) => {
    const [activeSideBar,setActiveSideBar] = useState(true);
    const toggleSidebar = () => {
        setActiveSideBar((prev) => prev = !prev);
        sendSidebar(activeSideBar)
    };
    function sendSidebar(active:boolean) {
        recieveSideToggle(active);
    }
    return (
    <div className='w-[80%] h-fit px-[96px] py-[60px] flex flex-col gap-4'>
        <div className='w-full h-fit'>
            {
                activeSideBar ?
                (<TbLayoutSidebarLeftCollapse  className='w-[24px] h-[24px] cursor-pointer' onClick={() => toggleSidebar()}/>) :
                (<TbLayoutSidebarRightCollapse className='w-[24px] h-[24px] cursor-pointer' onClick={() => toggleSidebar()}/>)
            }
        </div>
        <video autoPlay controls preload="none" className="object-cover w-full h-full">
            <source src="https://res.cloudinary.com/dl3kraaox/video/upload/v1755017924/pexels-tea-oebel-6804109_ovfljo.mp4" type="video/mp4" />
        </video>
    </div>
    )
}

export default Content;